
.login-page {
  padding-top: 180px;
  height: 100%;
  background-image: linear-gradient(-135deg, #C6ADD1 0%, #9DAFD0 50%, #ABA9CE 100%);
}
.login-page .login-container {
    width: 300px;
    margin: 0 auto;
}
.login-page .login-container .title {
      margin: 0px auto 40px auto;
      text-align: center;
      color: #505458;
}
.login-page .login-container .el-form-item {
      position: relative;
      margin-bottom: 20px;
}
.login-page .login-container .el-form-item .el-input-myprepend {
        position: absolute;
        top: 0;
        left: 0;
        width: 50px;
        height: 28px;
        margin-top: 11px;
        line-height: 28px;
        padding: 0 20px;
        color: #8684BC;
        border-right: 1px solid #B5B5CD;
}
.login-page .login-container .el-form-item .el-input {
        width: 100%;
        height: 50px;
}
.login-page .login-container .el-form-item .el-input .el-input__inner {
          height: 100%;
          border-radius: 50px;
          padding-left: 65px;
}
.login-page .login-container .el-button {
      height: 40px;
      background-image: linear-gradient(-90deg, #AA84BC 0%, #8684BC 100%);
      border-radius: 25px;
}



