#pagination .page[data-v-dbd5e602] {
  min-width: 72px;
  float: left;
  height: 24px;
  line-height: 24px;
}
#pagination > ul[data-v-dbd5e602] {
  float: left;
  margin-left: 10px;
}
#pagination > ul li[data-v-dbd5e602] {
    height: 24px;
    box-sizing: border-box;
    border: 1px solid #B5B5CD;
    border-right: 0;
    float: left;
    line-height: 22px;
    text-align: center;
    color: #8684BC;
}
#pagination > ul li[data-v-dbd5e602]:first-child {
      border-bottom-left-radius: 6px;
      border-top-left-radius: 6px;
}
#pagination > ul li[data-v-dbd5e602]:last-child {
      border-top-right-radius: 6px;
      border-bottom-right-radius: 6px;
}
#pagination > ul .box[data-v-dbd5e602] {
    display: block;
    width: 40px;
    background-color: #FFFFFF;
    background: -ms-linear-gradient(top, #FFFFFF, #B5B5CD);
    /* IE 10 */
    background: -moz-linear-gradient(top, #FFFFFF, #B5B5CD);
    /*火狐*/
    background: -webkit-gradient(linear, 0% 100%, 100% 100%, from(#FFFFFF), to(#B5B5CD));
    /*谷歌*/
    background: -webkit-gradient(linear, 0% 100%, 100% 100%, from(#FFFFFF), to(#B5B5CD));
    /* Safari 4-5, Chrome 1-9*/
    background: -webkit-linear-gradient(top, #FFFFFF, #B5B5CD);
    /*Safari5.1 Chrome 10+*/
    background: -o-linear-gradient(top, #FFFFFF, #B5B5CD);
    /*Opera 11.10+*/
}
#pagination > ul .box[data-v-dbd5e602]:last-child {
      border-right: 1px solid #B5B5CD;
}
#pagination > ul .input[data-v-dbd5e602] {
    float: left;
    width: 65px;
    background-color: #ffffff;
}
#pagination > ul .input input[data-v-dbd5e602] {
      width: 100%;
      height: 20px;
      border: 0;
      outline: 0;
      text-align: center;
}
#pagination > ul .btn[data-v-dbd5e602] {
    width: 40px;
    background-color: #8684BC;
    color: #ffffff;
}



